@import url("//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css");
.animated_icons {
  position: relative;
  display: flex;
  // transform: rotate(deg) skew(25deg);
  transform-style: preserve-3d;
}
.animated_icons li {
  position: relative;
  list-style: none;
  width: 60px;
  height: 60px;
  margin: 0px 20px;
}
.animated_icons li:before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: -5px;
  width: 100%;
  height: 10px;
  background: #2a2a2a;
  trnasform-origin: top;
  transform: skewX(-41deg);
}
.animated_icons li:after {
  content: "";
  position: absolute;
  top: 5px;
  left: -9px;
  width: 9px;
  height: 100%;
  background: #2a2a2a;
  trnasform-origin: right;
  transform: skewY(-49deg);
}
.animated_icons li span {
  position: absolute;
  top: 0;
  lef: 0;
  width: 100%;
  height: 100%;
  display: flex !important;
  background: #2a2a2a;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 30px !important;
  transition: 1.5s ease-out;
}
.animated_icons li:hover span {
  z-index: 1000;
  transition: 0.3s;
  color: #fff;
  box-shadw: -1px 1px 1px rgba(0, 0, 0, 0.5);
}
.animated_icons li:hover span:nth-child(5) {
  transform: translate(40px, -40px);
  opacity: 1;
}
.animated_icons li:hover span:nth-child(4) {
  transform: translate(30px, -30px);
  opacity: 0.8;
}
.animated_icons li:hover span:nth-child(3) {
  transform: translate(20px, -20px);
  opacity: 0.6;
}
.animated_icons li:hover span:nth-child(2) {
  transform: translate(10px, -10px);
  opacity: 0.4;
}
.animated_icons li:hover span:nth-child(1) {
  transform: translate(0px, 0px);
  opacity: 0.2;
}
.animated_icons li:nth-child(1):hover span {
  background: #52e19f !important;
}
.animated_icons li:nth-child(2):hover span {
  background: #2c3456 !important;
}
.animated_icons li:nth-child(3):hover span {
  background: #ea6e96 !important;
}
.animated_icons li:nth-child(4):hover span {
  background: #fceb00 !important;
}
